"use client"

import Card from './Card'

export default function ChaptersSection() {
  const cards = [
    {
      id: 1,
      imageSrc: '/cell.webp',
      altText: 'Celula si Organitele Celulare',
      title: 'Celula si Organitele Celulare',
      description: 'Celula reprezintă unitatea morfo-funcțională și genetică a organizării materiei vii. Ca formă de existență, aceasta poate funcționa individual sau în grup, atunci când contribuie la formarea țesuturilor.',
      linkUrl: '/details/1',
    },
    {
      id: 2,
      imageSrc: '/images/image-2.jpg',
      altText: 'Description for image 2',
      title: 'Card Title 2',
      description: 'This is the description for card 2.',
      linkUrl: '/details/2',
    },
  ];

  return (
  <section className="relative w-full h-screen flex justify-center">
    <div className="container mx-auto p-4">
      <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
        {cards.map((card) => (
          <Card key={card.id} {...card} />
        ))}
      </div>
    </div>
  </section>
  );
}